import { Component, OnInit } from '@angular/core';
import { CompanyPerizinanService } from 'src/app/sdk/hr/services';
import { Perizinan } from 'src/app/sdk/hr/models';
import { ActivatedRoute, Router } from '@angular/router';
import { NgxSpinnerService } from 'ngx-spinner';
import { VendorVendorService, VendorContactService } from 'src/app/sdk/asset/services';
import { Vendor, Contact } from 'src/app/sdk/asset/models';

@Component({
  selector: 'app-detail',
  templateUrl: './detail.component.html',
  styleUrls: ['./detail.component.scss']
})
export class DetailComponent implements OnInit {

    constructor(
        private routerActive: ActivatedRoute,
        private companyPerizinanService: CompanyPerizinanService,
        private vendorVendorService: VendorVendorService,
        private vendorContactService: VendorContactService,
        private spinner: NgxSpinnerService,
        private router: Router,
    ) { }



    dataPerizinan: Perizinan = {}
    dataVendor: Vendor = {}


    id_perizinan: string = ''

    ngOnInit() {
        this.id_perizinan = this.routerActive.snapshot.paramMap.get('id')
        this.getLisensi();


    }

    getLisensi() {
        this.spinner.show()
        this.companyPerizinanService.getCompanyPerizinansId(this.id_perizinan).subscribe(
            response => {
                this.dataPerizinan = response.data;
                this.getVendor(this.dataPerizinan.id_perusahaan)
                this.getContact(this.dataPerizinan.contact)
                this.spinner.hide()
            }
        )
    }

    dataContact: Contact = {}
    getContact(contact) {
        this.vendorContactService.getVendorContactsId(contact).subscribe(
            data=> {
                this.dataContact = data.data
            }
        )
    }

    getVendor(id_perusahaan) {
        this.vendorVendorService.getVendorVendorsId(id_perusahaan).subscribe(
            data=> {
                this.dataVendor=data.data
            }
        )
    }

    goToBack() {

        this.router.navigate(['/hr/company/module/legal/list'])
    }

}
