import { Perizinan } from 'src/app/sdk/hr/models/perizinan';
import { CompanyPerizinanService } from 'src/app/sdk/hr/services';
import { Component, OnInit } from '@angular/core';
import { FilterFuctionService } from 'src/app/services/filter-fuction.service';
import { NzMessageService } from 'ng-zorro-antd';
import { NgxSpinnerService } from 'ngx-spinner';


@Component({
    selector: 'app-list',
    templateUrl: './list.component.html',
    styleUrls: ['./list.component.scss']
})
export class ListComponent implements OnInit {

    constructor(
        private companyPerizinanService: CompanyPerizinanService,
        private filterFuctionService: FilterFuctionService,
        private nzMessageService: NzMessageService,
        private spinner: NgxSpinnerService
    ) { }

    ngOnInit() {
        this.getData()
    }

    listOfDisplayData: Perizinan[] = []
    listOfData: Perizinan[] = []
    sortKey: string | null = '';
    sortValue: string | null = '';
    sort(sort: { key: string; value: string }): void {
        this.sortKey = sort.key;
        this.sortValue = sort.value;
        this.getData();
    }
    search: string = "";

    getData() {
        this.spinner.show()
        this.companyPerizinanService.getCompanyPerizinans(JSON.stringify({sortKey: this.sortKey, sortValue: this.sortValue, search: this.search})).subscribe(
            data => {
                this.listOfDisplayData = data.data;
                this.listOfData = data.data;
                this.spinner.hide()

            }
        )
    }

    reset(): void {
        this.search = '';
        this.getData();
    }

    delete(id) {
        this.spinner.show();
        this.companyPerizinanService.deleteCompanyPerizinansId(id).subscribe(
            data => {
                this.getData();
                this.spinner.hide();
            }
        )
    }

    batalDelete() {
        this.nzMessageService.info('click cancel');
    }

}
