import { Perusahaan, Kantor } from 'src/app/sdk/hr/models';
import { Perizinan } from 'src/app/sdk/hr/models/perizinan';
import { CompanyPerizinanService, CompanyPerusahaanService, CompanyKantorService } from 'src/app/sdk/hr/services';
import { Component, OnInit } from '@angular/core';
import { FormBuilderTypeSafe, FormGroupTypeSafe } from 'src/app/services/angular-reactive-forms-helper';
import { PesanService } from 'src/app/services/tool/pesan.service';
import { NgxSpinnerService } from 'ngx-spinner';
import { Router, ActivatedRoute } from '@angular/router';
import { FormControl, Validators } from '@angular/forms';
import { SingleUploadService } from 'src/app/components/document/single-upload.service';
import { VendorVendorService, VendorContactService } from 'src/app/sdk/asset/services';
import { Vendor, Contact } from 'src/app/sdk/asset/models';

@Component({
    selector: 'app-edit',
    templateUrl: './edit.component.html',
    styleUrls: ['./edit.component.scss']
})
export class EditComponent implements OnInit {

    constructor(
        private fb: FormBuilderTypeSafe,
        private pesan: PesanService,
        private companyPerizinanService: CompanyPerizinanService,
        private companyPerusahaanService: CompanyPerusahaanService,
        private companyKantorService: CompanyKantorService,
        private vendorContactService: VendorContactService,
        private vendorVendorService: VendorVendorService,
        private spinner: NgxSpinnerService,
        private router: Router,
        private activatedRoute: ActivatedRoute,
        private singleUploadService: SingleUploadService,
    ) { }

    data: Perizinan = {};
    toogleUpload = false
    ngOnInit() {
        this.id = this.activatedRoute.snapshot.paramMap.get('id');
        this.form();
        this.getPreeData()
        this.getData()

        this.singleUploadService.getCallback().subscribe(
            data => {

                if (data == 'company_legal.finish') {
                    this.toogleUpload = false;
                }
            }
        )

        this.singleUploadService.getId().subscribe(
            data => {
                if (data) {
                    this.dataForm.patchValue({ id_dokumen: data })
                    this.simpan()
                }
            }
        )

    }

    togleShowUpload() {

        this.toogleUpload = true;
    }

    id: string = "";
    getData() {
        this.spinner.show()
        this.companyPerizinanService.getCompanyPerizinansId(this.id).subscribe(
            data => {
                this.dataForm.setValue(data.data)
                this.spinner.hide()
                this.getContact()
                this.singleUploadService.param.next({
                    for_module: "company_legal",
                    for_table: "erp.perizinan",
                    for_module_id: data.data.id_perizinan
                })
            }
        )

    }



    listVendor: Vendor[] = [];
    listKantor: Kantor[] = [];
    listContact: Contact[] = [];
    getPreeData() {
        this.vendorVendorService.getVendorVendors().subscribe(
            data => {
                this.listVendor = data.data;
            }
        )
        this.companyKantorService.getCompanyKantors().subscribe(
            data => {
                this.listKantor = data.data;
            }
        )
    }
    a = 0
    getContact() {
        this.vendorContactService.getVendorContacts(JSON.stringify({ ref_id_module: this.dataForm.value.id_perusahaan })).subscribe(
            data => {
                this.listContact = data.data
                if (this.a > 1) {
                    this.dataForm.get('contact').setValue(null)
                }
                this.a++;
                console.log(this.a)
            }
        )
    }


    dataForm: FormGroupTypeSafe<Perizinan>;
    form() {
        this.dataForm = this.fb.group<Perizinan>(
            {
                id_kantor: new FormControl(''),
                id_perizinan: new FormControl(''),
                id_dokumen: new FormControl(''),
                id_perusahaan: new FormControl(''),
                tanggal_berakhir: new FormControl(''),
                tanggal_berlaku: new FormControl(''),
                tentang_izin: new FormControl(''),
                status_izin: new FormControl(''),
                contact: new FormControl(''),
                id_user_create: new FormControl(''),
                created_at: new FormControl(''),
                updated_at: new FormControl(''),
                nomor_izin: new FormControl('')
            }
        )
    }

    simpan() {
        this.spinner.show()
        if (this.dataForm.invalid) {
            this.pesan.pesanWarningForm('Form Tidak Valid');
            this.spinner.hide()
            return;
        }

        this.data = this.dataForm.value;
        this.companyPerizinanService.putCompanyPerizinansId({
            id: this.id,
            body: this.data
        }).subscribe(
            data => {
                this.goToBack()
                this.spinner.hide()
            }, err => {
                this.pesan.pesanWarningForm('Gagal di simpan');
                this.spinner.hide()
            },
            () => {
                console.log('HTTP request completed.')
            }
        )

    }

    goToBack() {
        this.router.navigate(['/hr/company/module/legal/list'])
    }

}
